body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@CHARSET "UTF-8";

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font: inherit;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
    display: block;
}
body {
    line-height: 1;
}
ol, ul {
    list-style: none;
}
blockquote, q {
    quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}
@font-face {
    font-family: "MapleStory";
    font-style: normal;
    font-weight: 700;
    src: url(/static/media/MaplestoryBold.b0a6a269.ttf);
}

@font-face {
    font-family: "MapleStory";
    font-style: normal;
    font-weight: normal;
    src: url(/static/media/MaplestoryLight.fb932d04.ttf);
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
input, select, button,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    font-family: 'MapleStory', sans-serif, Consolas, Serif;
    font-style: normal;
    font-weight: normal;
}
* {
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
}

/*스크롤 고정 및 html, body 전체영역*/
html, body {
    height: 100%;
    width:100%;
    overflow: hidden;
}
#root{
    overflow: auto;
    margin-top: 100px;
    height: 100%;
    text-align: center;
}

#root h4{
    font-size: 22pt;
    font-weight: 700;
}
#root > .center_box{
    width: 550px;
    min-height: 250px;
    box-shadow: 0 0 15px 5px #999;
    display: inline-block;
    position: relative;
    text-align: left;
    margin-top: 55px;
    padding: 20px;
    border-radius: 20px;
}
#root > .result_box{
    width: 1200px;
    display: inline-block;
    position: relative;
    margin-top: 55px;
    padding: 0 15px 300px 15px;
}

.total_view{
    text-align: right;
}

.day_view{
    text-align: center;
    margin-top: 20px;
}

.day_view > ul{
    width: 150px;
    border: 4px solid #C13E31;
    display: inline-block;
    margin-right: 15px;
    margin-bottom: 15px;
    border-radius: 15px;
    padding: 10px;
    box-shadow: 0 0 6px -1px #fea03c;
    transition: 0.5s;
}

.day_view > ul:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
}
.day_view > ul > li:first-child{
    font-weight: 700;
    font-size: 14pt;
}
.day_view > ul > li:not(:first-child){
    text-align: left;
    margin-top: 5px;
}

.modalPopup{
    width: 100%;
    height: 100%;
    z-index: 9999;
    position: absolute;
    top: 0;
    transition: 1s;
}
.modalPopup_background{
    width: 100%;
    height: 100%;
    z-index: 0;
    position: absolute;
    top: 0;
    background: #ccc;
    opacity: 0.8;
}
.modalPopup_content{
    position: relative;
    width: 80%;
    height: 70%;
    z-index: 9999;
    margin-top: 5%;
    padding:10px;
    display: inline-block;
    background: #fff;
    border: 3px solid #fea03c;
    border-radius: 15px;
}
.popupDiv{
    width: 100%;
    height: calc(100% - 45px);
}

.content_layer{
    overflow-y: auto;
    height: calc(100% - 90px);
}

.cube_img_layer{}
.cube_img_layer > .cube_imgs{
    width: 70px;
    height: 70px;
    transition: 0.5s;
}
.cube_img_layer > .cube_imgs:hover{
    cursor: pointer;
    scale: 1.1;
}

.list_ul{

}
.list_ul li{
    display: inline-block;
    min-width: 120px;
    font-size: 14pt;
}
.list_ul li:not(:first-child){
    margin-left: 15px;
}

.popupTable {
    width: 100%;
    table-layout: fixed;
    border-spacing: 0;
    border-collapse: collapse;
}
.popupTable thead tr {
    position: absolute;
    top: 0;
}
.popupTable thead tr th {
    height: 40px;
    background:#CEFFA7;
    color:#333;
    font-weight: 700;
    font-size: 14pt;
    vertical-align: middle;
}

.table-box-wrap {
    position: relative;
    margin-top: 20px;
    padding-top: 40px;
    border: 1px solid #ccc;
}
.table-box-wrap .table-box {
    max-height: 440px;
    overflow: auto;
    overflow-x: hidden;
}

.popupTable td {
    text-align: center;
    height: 40px;
    border-top: 1px solid #ccc;
    vertical-align: middle;
}

.popupTable tr {
    display: table;
    width: 100%;
    table-layout: fixed;
}

.popupTable tbody tr {
    display: table-row;
}

.header{
    position: absolute;
    top: 0;
    width: 100%;
    height: 100px;
    line-height: 28px;
    padding: 10px;
    text-align: center;
    background: #fff;
}

.hidden{
    display: none;
}

h2{
    text-align: center;
    margin-top: 35px;
    font-size: 36pt;
    font-weight: 700;
}

.footer{
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 100px;
    line-height: 28px;
    font-size: 14pt;
    padding: 10px;
    text-align: center;
    background: #282c34;
    color: #eee;
}
.footer > font{
    font-weight: 700;
    display: block;
}

textarea{
    width: 100%;
    height: 100px;
    outline: none;
    padding-left: 5px;
    border: 2px solid #999;
    border-radius: 5px;
    margin-top: 10px;
    resize: none;
}

button{
    border: none;
    padding: 10px 20px;
    font-weight: 700;
    transition: 0.5s;
    color: #333;
    background: #61dafb;
    border-radius: 5px;
}
button:hover{
    background: #C13E31;
    font-weight: 700;
    color: #eee;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
}

.btn_div{
    text-align: center;
    margin: 10px 0;
}

@-webkit-keyframes spinner {
    from {-webkit-transform: rotate(0deg);transform: rotate(0deg); }
    to {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}

@keyframes spinner {
    from {-webkit-transform: rotate(0deg);transform: rotate(0deg); }
    to {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}

#loading {
    width: 100%;
    height: calc(100% - 100px);
    position: absolute;
    top:0;
    left:0;
}

.spinner::after {
    content: '';
    box-sizing: border-box;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 64px;
    height: 64px;
    margin-top: -32px;
    margin-left: -32px;
    border-radius: 50%;
    border: 4px solid lightgrey;
    border-top-color: #C13E31;
    -webkit-animation: spinner .8s linear infinite;
            animation: spinner .8s linear infinite;
}
